<template>
  <v-dialog
    v-model="show"
    :fullscreen="fullScreen"
    max-width="600"
    transition="dialog-bottom-transition"
    scrollable>
    <v-card>
      <v-toolbar class="elevation-0 white--text" color="primary">
        <v-toolbar-title>Update Picture</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click.native="close()" dark>
          <v-icon>close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-card-text class="pa-4">
        <v-layout v-if="step === 1" row wrap justify-center>
          <v-flex>
            <div
              @click="handleClick"
              @drop="handleChange"
              @dragover="preventDefault"
              @dragenter="preventDefault"
              @dragleave="preventDefault">
              <v-container class="image-drop-area grey lighten-4"
                fluid fill-height>
                <v-layout column justify-center align-center>
                  <v-alert :value="!showError" outline color="grey lighten-1" icon="add_a_photo" >
                    Click or drag image file here
                  </v-alert>
                  <v-alert :value="showError" outline color="error" icon="add_a_photo" >
                    {{ errorMessage }} Select another image.
                  </v-alert>
                  <div class="caption grey--text">200px by 200px image recommended</div>
                </v-layout>
              </v-container>
              <input type="file" v-show="false" @change="handleChange" ref="fileInput" accept="image/*">
            </div>
          </v-flex>
        </v-layout>
        <v-layout v-if="step === 2" row wrap justify-center>
          <v-flex xs12 sm8>
            <v-card class="pa-0 ma-0 elevation-2" color="grey darken-2">
              <v-card-media align-center justify-center>
                <v-layout justify-center>
                  <img
                    v-if="sourceImgUrl.length > 0"
                    :src="sourceImgUrl"
                    class="image-preview-area pa-0 ma-0 ">
                  <v-container
                    v-if="sourceImgUrl.length === 0"
                    class="image-preview-area grey lighten-4 pa-0 ma-0"
                    fluid fill-height>
                  </v-container>
                </v-layout>
              </v-card-media>
            </v-card>
            <v-slider v-if="false"
              append-icon="zoom_in"
              :append-icon-cb="() => previewZoom += 10"
              prepend-icon="zoom_out"
              :prepend-icon-cb="() => previewZoom -= 10"
              v-model="previewZoom">
            </v-slider>
          </v-flex>
          <v-flex xs12 sm4>
            <v-card class="pa-0 ma-0 elevation-0 transparent">
            <v-layout row wrap justify-center align-center>
              <v-avatar
                v-if="false"
                class="my-2 mx-4 elevation-2"
                tile
                :size="90">
                <template v-if="sourceImgUrl.length > 0">
                  <img :src="sourceImgUrl">
                </template>
                <template v-else>
                  <img src="/static/avatar_placeholder.png">
                </template>
              </v-avatar>
              <v-avatar
                class="my-2 mx-4 elevation-2"
                :size="90">
                <template v-if="sourceImgUrl.length > 0">
                  <img :src="sourceImgUrl">
                </template>
                <template v-else>
                  <img src="/static/avatar_placeholder.png">
                </template>
              </v-avatar>
            </v-layout>
            </v-card>
          </v-flex>
        </v-layout>
      </v-card-text>
      <div v-if="step === 2">
        <v-card-actions class="px-4 pb-4">
          <v-spacer></v-spacer>
          <v-btn @click="step = 1" class="mr-3">Back</v-btn>
          <v-btn @click="onUpload()" color="primary">Save</v-btn>
        </v-card-actions>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
/* eslint-disable */
'use strict'
import config from '../../config'
import AWS, { S3 } from 'aws-sdk'
import layout from '../Layout'
import router from '../../routes'

export default {
  props: {
    dialog: Boolean
  },
  data: function () {
    return {
      step: 1,
      loading: 0,
      progress: 0,
      errorMessage: 'An error occured!',
      showError: false,
      sourceImgUrl: '',
      fileName: '',
      user:'',
      previewZoom: 0,
      fullScreen: true,
      valid: false,
      loader: false,
      maxSize: 102400,
      s3: new AWS.S3({
        apiVersion: '2012-10-17',
        params: { Bucket: config.bucketname }
      })
    }
  },
  methods: {
    preventDefault (event) {
      event.preventDefault()
      return false
    },
    handleClick (event) {
      if (this.loading !== 1) {
        if (event.target !== this.$refs.fileInput) {
          event.preventDefault()
          if (document.activeElement !== this.$refs) {
            this.$refs.fileInput.click()
          }
        }
      }
    },
    handleChange (event) {
      event.preventDefault()
      if (this.loading !== 1) {
        let files = event.target.files || event.dataTransfer.files
        this.reset()
        if (this.checkFile(files[0])) {
          this.setSourceImg(files[0])
        }
      }
    },
    checkFile (file) {
      if (file.type.indexOf('image') === -1) {
        this.showError = true
        this.errorMessage = 'The selected file is not an image! '
        return false
      }
      if (file.size / 1024 > this.maxSize) {
        this.errorMessage = 'Exceeded maximum size of ' + this.maxSize + ' kb'
        this.showError = true
        return false
      }
      return true
    },
    reset () {
      this.loading = 0
      this.progress = 0
      this.showError = false
      this.errorMessage = ''
      this.errorMessage = 'An error occured!'
      this.showError = false
      this.sourceImgUrl = ''
      this.step = 1
    },
    setSourceImg (file) {
      let reader = new FileReader()
      this.fileName = file.name
      console.log(file +'filename: ' + file.name)
      reader.onload = (event) => {
        this.sourceImgUrl = event.target.result
        // console.log("source" + this.sourceImgUrl);
        this.step = 2
      }
      reader.readAsDataURL(file)
    },
    onUpload () {
      // this.configUpdate()
      this.configUpdate();
      let ext = ''
      if (~this.fileName.indexOf('.jpg')) {
        ext = '.jpg'
      } else if (~this.fileName.indexOf('.jpeg')) {
        ext = '.jpeg'
      } else if (~this.fileName.indexOf('.png')) {
        ext = '.png'
      } else if (~this.fileName.indexOf('.bmp')) {
        ext = '.mbp'
      } else {
        console.error('file type not supported')
        return
      }

      // const fileName = this.username + '_' + Date.now().toString() + ext
      const fileName = this.username + '.png';
      console.log('uploading file: ' + fileName)
      var data = this.sourceImgUrl.replace(/^data:image\/\w+;base64,/, "");
      var buf = new Buffer(data, 'base64');
      const params = {
        Key: fileName,
        Body: buf,
        ContentType: 'png',
        ContentEncoding: 'base64',
        Bucket: 'pictureupload1'
      }
      console.log(params)
      this.s3.upload(params, function (err, data) {
        console.log()
        if (err) {
          console.error(err.message)
        } else {
          console.log('Successfully uploaded photo.')
          router.push('/home');
        }
      })
      this.$emit('close')
      this.reset()
    },
    close: function () {
      this.$emit('close')
      this.reset()
    },
    setHeaders (param) {
      if (param === 'xs') {
        this.fullScreen = true
      } else {
        this.fullScreen = false
      }
    },
    configUpdate () {
      const idToken = this.$store.state.auth.tokens.idToken
      console.log('config update')
      // console.log(idToken)
      // localStorage.setItem('email', this.$store.state.auth.username)
      // this.user = this.$store.state.auth.username
      console.log(this.user)
      let credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: config.poolID,
        RoleArn: 'arn:aws:iam::308566621904:role/Cognito_app_nameAuth_Role',
        Logins: {
          'cognito-idp.us-west-2.amazonaws.com/us-west-2_ke2SDNHhn': idToken
        },
        LoginId: this.$store.state.auth.username
      })
      console.log('credential' +(credentials))
      AWS.config.update({
        region: config.region,
        credentials: credentials
      })
    this.s3 = new AWS.S3({
        apiVersion: '2012-10-17',
        params: { Bucket: config.bucketname }
      })
      this.listObjects();
    },
    listObjects (){
      let albums= [];
      const params = {
        // Key: fileName,
        // Body: buf,
        // ContentType: 'png',
        // ContentEncoding: 'base64',
        Bucket: 'pictureupload1'
      }
      this.s3.listObjects(params, function(err, data)
      {
        if (err) 
           return alert("There was an error listing your albums: " + err.message); // an error occurred
        else {
           for (let i = 0; i < data.Contents.length; i++) {
             albums.push( data.Contents[i].Key);
         }   
          console.log(albums);  
          return albums;
        }
           });
    }
    // listObjects () {
    //   var albums;
    //   console.log(this.$store.state.auth.username)
    //   let a = this.$store.state.auth.username;
    //   this.user = a.split("@")
    //   this.configUpdate();
    //    this.s3.listObjects({ Delimiter: "/" }, function(err, data) { 
    //     if (err) {
    //       return alert("There was an error listing your albums: " + err.message);
    //     } else {
    //       console.log(data)
    //     }
    //   }
    
    //   var albums;
    //   console.log(this.$store.state.auth.username)
    //   let a = this.$store.state.auth.username;
    //   this.user = a.split("@")
    //   this.configUpdate();
    //    this.s3.listObjects({ Delimiter: "/" }, function(err, data) { 
    //     if (err) {
    //       return alert("There was an error listing your albums: " + err.message);
    //     } else {
    //       console.log(data)
    //      let i;
    //      for (i = 0; i < data.Contents.length; i++) {
    //         albums = data.Contents[i].Key;
    //      }
    //     //  var split_album = albums.split("_")
    //     var split_album = albums.split("_");
    //     console.log(split_album[0]);
    //     this.user = a.split("@")
    //     console.log("user"+this.user[0])
    //     for(let i =0; i < album.length; i++){
    //       if (this.user[0] == split_album[0]){
    //         console.log("OKASY")
    //         this.s3.deleteObject({ Key: photoKey }, function(err, data) {
    //         if (err) {
    //           return alert("There was an error deleting your photo: ", err.message);
    //         }
    //         alert("Successfully deleted photo.");
    //         viewAlbum(albumName);
    //         });
    //       }
    //     }
        //  let split_user = this.LoginId.split("@")
        //  if(abc[0] === split_user){
        //    console.log("Hello")
        //  }
        //   let albums = data.CommonPrefixes.map(function(commonPrefix) {
        //   let prefix = commonPrefix.Prefix;
        //   let albumName = decodeURIComponent(prefix.replace("/", ""));
        //   console.log(albums)
        // });
    //     }
    //    });
    // }
  },
  computed: {
    breakpoint () {
      return this.$vuetify.breakpoint.name
    },
    show () {
      return this.dialog
    },
    username () {
      let str = this.$store.state.auth.username
      var index = str.indexOf('@')
      return str.substring(0, index)
    }
  },
  watch: {
    breakpoint () {
      this.setHeaders(this.breakpoint)
    }
  },
  created () {
    this.setHeaders(this.breakpoint)
  }
}
</script>
<style scoped>
.image-drop-area {
  height: 240px;
}
.image-preview-area {
  max-height: 240px;
  width: auto;
  height: 100%;
}
</style>



