import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'
/* authentication */
import SignIn from '@/components/Auth/Signin'
import Register from '@/components/Auth/Register'
import Registered from '@/components/Auth/Registered'
import Forgot from '@/components/Auth/Forgot'
import Confirm from '@/components/Auth/Confirm'
import Changed from '@/components/Auth/Changed'
import Resend from '@/components/Auth/Resend'
/* Profiles */
import Profile from '@/components/Profile/Index'
import state from './store/module/authenticate'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
      // beforeEnter: (to, from, next) => {
      //   if (store.state.authenticated === false) {
      //     console.log(store.state.authenticated)
      //     next('/signin')
      //   } else {
      //     next('/home')
      //   }
      // }
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      beforeEnter: (to, from, next) => {
        // console.log(state.state.authenticated)
        if (state.state.authenticated === true) {
          next()
        } else {
          next('/signin')
        }
      }
    },
    /* Authentication */
    { path: '/signin', name: 'SignIn', component: SignIn },
    {
      path: '/register',
      name: 'Register',
      component: Register,
      beforeEnter: (to, from, next) => {
        if (state.state.authenticated === true) {
          next()
        } else {
          next('/signin')
        }
      }
    },
    {
      path: '/registered', name: 'Registered', component: Registered },
    {
      path: '/forgot',
      name: 'Forgot',
      component: Forgot
    },
    // {
    //   path:'/other',
    //    beforeEnter: (to, from, next) => {
    //     if (store.state.authenticated !== true) {
    //       console.log(store.state.authenticated)
    //       next('/signin')
    //     } else {
    //       next('/other');
    //     }
    //   }
    // },
    {
      path: '/confirm',
      name: 'Confirm',
      component: Confirm
      // beforeEnter: (to, from, next) => {
      //   if (store.state.authenticated !== true) {
      //     console.log(store.state.authenticated)
      //     next('/signin')
      //   } else {
      //     next('/confirm')
      //   }
      // }
    },
    {
      path: '/changed',
      name: 'Changed',
      component: Changed
      // beforeEnter: (to, from, next) => {
      //   if (store.state.authenticated !== true) {
      //     console.log(store.state.authenticated)
      //     next('/signin')
      //   } else {
      //     next('/changed')
      //   }
      // }
    },
    {
      path: '/resend',
      name: 'Resend',
      component: Resend
      // beforeEnter: (to, from, next) => {
      //   if (store.state.authenticated !== true) {
      //     console.log(store.state.authenticated)
      //     next('/signin')
      //   } else {
      //     next('/resend')
      //   }
      // }
    },
    /* Profiles */
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
      beforeEnter: (to, from, next) => {
        if (state.state.authenticated === true) {
          next()
        } else {
          next('/signin')
        }
      }
    }
  ],
  mode: 'history'
})



