<template>
  <v-container fluid fill-height>
    <v-layout column align-center justify-center class="mb-5">
      <!-- <transition-group appear name="fadeout"> -->
        <div>
        <!-- <img class="aws-logo mb-4" src="../../static/aws_cognito_vue.png"> -->
        <v-jumbotron
          :gradient="gradient"
          dark
          src="https://cdn.vuetifyjs.com/images/parallax/material2.jpg">
          <v-container fill-height>
            <v-layout align-center>
              <v-flex text-xs-center>
                <h3 class="display-3">CLOUD SIMULATION</h3>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
        <br>
        </div>
      <!-- </transition-group> -->
      <v-btn v-if="!isAuthenticated" color="amber accent-3" dark large @click="navSignIn()" class="mb-5">Get Started</v-btn>
      <v-alert v-if="isAuthenticated" outline type="success" class="mt-4 mb-4" :value="true">
        <div class="title mt-2 mb-2">You have signed in.</div>
      </v-alert>
      <v-btn v-if="isAuthenticated" @click="navSignOut()" color="amber accent-3" dark large class="mb-5">Sign Out</v-btn>
    </v-layout>
  </v-container>
</template>

<script>
import router from '../routes'

export default {
  data () {
    return {
      gradient: 'to top right, rgba(192,192,192,0.3), rgba(25,32,72, .7)'
    }
  },
  computed: {
    isAuthenticated: function () {
      return this.$store.state.auth.authenticated
    }
  },
  methods: {
    navRegister: function () {
      console.log('navigatig to sign up')
      router.push('/register')
    },
    navSignIn: function () {
      console.log('navigatig to sign in')
      router.push('/signin')
    },
    navSignOut: function () {
      this.$store.dispatch('signOut')
      router.push('/')
    }
  }
}
</script>

<style>
.aws-logo {
  max-height: 80px;
}
</style>



